html,
body {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 500px;
  /*min-height: 500px;*/
}
#app {
  display: block;
  width: 100%;
}
.gallery {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  /*min-height: 500px;*/
}
.add-form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}
.form-row {
  -ms-flex: 1 1;
      flex: 1 1;
  text-align: center;
}
.form-row img {
